.app-w {
    width: 1200px;
    margin: 0 auto;
    display: grid;
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 10px;
    grid-template-areas:
"h h "
"n c";
}

.app-wrapper-content {
    grid-area: c;
    background-color: #f3d250;
    padding: 20px 40px;
    position: relative;
}

